.catalog {
  background-color: var(--background-color-lightest-grey);
}

.catalog__wrapper {
  padding: 50px 0 40px;

  @media (min-width: $tablet-width) {
    padding: 79px 0 85px;
  }

  @media (min-width: $desktop-width) {
    padding: 99px 0 116px;
  }
}

.catalog__grid-wrapper {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 318px 1fr;
    column-gap: 30px;
  }
}

.catalog__banner {
  text-align: center;
  margin-bottom: 5px;
}

.catalog__title {
  text-align: center;
  margin-bottom: 21px;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 39px;
  }
}

.catalog__filters {
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 44px;
  }
}

.catalog__select {
  margin-top: -4px;
  margin-bottom: 14px;

  @media (min-width: $tablet-width) {
    min-width: 282px;
    margin-bottom: 25px;
  }
}

.catalog__product-list {
  margin-bottom: 40px;

  @media (min-width: $tablet-width) {
    margin-bottom: 70px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 60px;
  }
}

.catalog__pagination {
  @media (min-width: $tablet-width) {
    display: grid;
  }
}
