/* GLOBAL */
:root {
  --main-font: "Montserrat", "Arial", sans-serif;
  --basic-color: #ffffff;
  --basic-color-black: #333333;
  --basic-color-grey: #bdbdbd;
  --basic-color-primary: #7859cf;
  --basic-color-secondary: #f0ebff;
  --alt-color-primary-opacity: rgba(120, 89, 207, 0.25);
  --alt-color-primary-full-opacity: rgba(120, 89, 207, 0);
  --alt-color-primary-lighter: #9070ec;
  --alt-color-primary-darker: #593cac;
  --alt-color-light-grey: #e2e2e2;
  --alt-color-black: rgba(255, 255, 255, 0);
  --background-color-lightest-grey: #f8f8f8;
  --background-color-flat-white: #f3ebe1;
  --background-color-primary-medium: #eae6fc;
  --background-color-grey: #e5e6e8;
  --backgroung-color-black-opacity: rgba(0, 0, 0, 0.15);
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 728px;
  }
}
@media (min-width: 1440px) {
  .container {
    width: 1360px;
  }
}

/* BLOCKS */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  display: block;
  font-family: var(--main-font);
  background-color: var(--basic-color-primary);
  color: var(--basic-color);
  border-radius: 4px;
  padding: 8px 20px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  box-sizing: border-box;
  border: none;
  text-decoration: none;
}
.button:hover, .button:focus {
  background-color: var(--alt-color-primary-lighter);
}
.button:active {
  background-color: var(--alt-color-primary-darker);
}
.button:disabled {
  cursor: default;
  background-color: var(--basic-color-grey);
}
.button--disabled {
  cursor: default;
  background-color: var(--basic-color-grey);
}
.button--reset {
  color: var(--basic-color-primary);
  background-color: var(--basic-color);
}
.button--reset:hover, .button--reset:focus, .button--reset:active {
  color: var(--basic-color);
}
.button--reset:disabled {
  cursor: default;
  color: var(--basic-color);
  background-color: var(--alt-color-light-grey);
}

.title {
  display: block;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 42px;
    line-height: 64px;
  }
}

.page {
  height: 100%;
  overflow-x: hidden;
}

.page__body {
  font-family: var(--main-font);
  background-color: var(--basic-color);
  color: var(--basic-color-black);
  margin: 0;
  min-height: 100%;
}

.navigation__menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.navigation__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .navigation__wrapper {
    align-items: center;
  }
}

@media (max-width: 767.98px) {
  .navigation > .container {
    width: 320px;
  }
}

.navigation__logo {
  display: block;
  height: 36px;
  margin: 12px 0 12px 20px;
}
@media (min-width: 768px) {
  .navigation__logo {
    margin: 22px 0;
  }
}

.navigation__toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  border: none;
  background-color: transparent;
  box-sizing: border-box;
  cursor: pointer;
  padding: 0;
}
.navigation__toggle:active {
  background-color: var(--basic-color-secondary);
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation--closed .navigation__toggle::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  background-image: url("../img/burger-menu.svg");
}

@media (max-width: 767.98px) {
  .navigation--closed .site-list {
    display: none;
  }
}

.navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
  position: absolute;
  content: "";
  width: 15px;
  height: 1px;
  background-color: var(--basic-color-black);
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}

.navigation--opened .site-list {
  background-color: var(--basic-color);
  position: absolute;
  top: 59px;
  z-index: 2;
  width: 100vw;
  margin-left: calc(50% - 50vw);
}

@media (max-width: 767.98px) {
  .navigation--nojs .navigation__toggle {
    display: none;
  }
}

@media (max-width: 767.98px) {
  .navigation--nojs .site-list {
    position: static;
    display: block;
    min-height: 0;
    width: 100vw;
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
  }
}

@media (max-width: 767.98px) {
  .navigation--nojs .site-list__item {
    display: block;
  }
}

