}

.user-list__link {
  display: flex;
  box-sizing: border-box;
  flex-wrap: wrap;
  padding: 22px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  cursor: pointer;
  color: var(--basic-color-black);
}
@media (min-width: 768px) {
