@media (min-width: 768px) {
  .information {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
  }
}

.information__title {
  margin-bottom: 20px;
}

.information__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.information__item {
  font-size: 12px;
  line-height: 16px;
  font-weight: 500;
  margin-bottom: 20px;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}
.information__item:last-child {
