}
@media (min-width: 768px) {
  .information__wrapper {
    padding: 0;
  }
}

.information__line-wrapper {
  position: relative;
}
.information__line-wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  height: 1px;
  width: 100vw;
  background-color: var(--basic-color-secondary);
  margin-left: calc(50% - 50vw);
}
@media (min-width: 768px) {
  .information__line-wrapper::before {
    display: none;
  }
}
.information__line-wrapper:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100vw;
  background-color: var(--basic-color-secondary);
  margin-left: calc(50% - 50vw);
}
@media (min-width: 768px) {
  .information__line-wrapper:last-child::after {
    display: none;
  }
