  }
}
@media (min-width: 1440px) {
  .contact__wrapper {
    min-width: 317px;
  }
}

.contact__icon {
  top: 2px;
  position: absolute;
}

.contact__address {
  display: block;
  margin: 0;
  font-size: 14px;
  line-height: 20px;
  margin-left: 20px;
}

.contact__phone {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  margin-left: 20px;
  text-decoration: none;
  color: var(--basic-color-black);
