.copyright {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.copyright__link {
  text-decoration: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 206px;
}

.copyright__logo-htmlacademy {
  display: block;
  fill: var(--basic-color-grey);
}

.copyright__link:hover .copyright__logo-htmlacademy {
  fill: var(--basic-color-primary);
}

.copyright__link:active .copyright__logo-htmlacademy {
  fill: var(--alt-color-primary-darker);
}

.copyright__label {
  font-size: 12px;
  line-height: 16px;
  color: var(--basic-color-grey);
}

.copyright__link:hover .copyright__label {
  color: var(--basic-color-primary);
}

.copyright__link:active .copyright__label {
  color: var(--alt-color-primary-darker);
}

.swiper-slide {
  background-color: var(--background-color-lightest-grey);
}
.swiper-slide--flat-white {
  background-color: var(--background-color-flat-white);
}
@media (min-width: 768px) {
  .swiper-slide--flat-white {
    background: linear-gradient(to bottom, var(--background-color-flat-white) 77%, var(--basic-color) 23%);
  }
}
@media (min-width: 1440px) {
  .swiper-slide--flat-white {
    background: none;
    background-color: var(--background-color-flat-white);
  }
}
.swiper-slide--lavander-latte {
  background-color: var(--background-color-primary-medium);
}
@media (min-width: 768px) {
  .swiper-slide--lavander-latte {
    background: linear-gradient(to bottom, var(--background-color-primary-medium) 77%, var(--basic-color) 23%);
  }
}
@media (min-width: 1440px) {
  .swiper-slide--lavander-latte {
    background: none;
    background-color: var(--background-color-primary-medium);
  }
}
.swiper-slide--triple-espresso {
  background-color: var(--background-color-grey);
}
@media (min-width: 768px) {
  .swiper-slide--triple-espresso {
    background: linear-gradient(to bottom, var(--background-color-grey) 77%, var(--basic-color) 23%);
  }
}
@media (min-width: 1440px) {
  .swiper-slide--triple-espresso {
    background: none;
    background-color: var(--background-color-grey);
  }
}

.swiper-button-prev,
.swiper-button-next {
  padding: 0;
  margin: 0;
  top: 0;
  width: 20px;
  height: 100%;
  border: none;
  background-color: transparent;
  color: transparent;
  text-indent: -9999px;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}
.swiper-button-prev::before,
.swiper-button-next::before {
  position: absolute;
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  background-color: var(--basic-color-primary);
  mask-image: url("../img/slide-arrow.svg");
  mask-repeat: no-repeat;
}
@media (min-width: 768px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 80px;
    height: 640px;
  }
}
@media (min-width: 1440px) {
  .swiper-button-prev,
  .swiper-button-next {
    height: 100%;
  }
}

.swiper-button-prev {
  left: 0;
}
.swiper-button-prev:hover, .swiper-button-prev:focus {
  background: linear-gradient(90deg, var(--basic-color) 0%, var(--alt-color-black) 100%);
}
.swiper-button-prev:active {
  background: linear-gradient(90deg, var(--alt-color-primary-opacity) 0%, var(--alt-color-primary-full-opacity) 100%);
}
.swiper-button-prev:disabled {
  cursor: default;
  background: transparent;
}
.swiper-button-prev:disabled::before {
  background-color: var(--basic-color-grey);
}

.swiper-button-next {
  right: 0;
}
.swiper-button-next::before {
  transform: rotate(180deg);
}
.swiper-button-next:hover, .swiper-button-next:focus {
  background: linear-gradient(-90deg, var(--basic-color) 0%, var(--alt-color-black) 100%);
}
.swiper-button-next:active {
  background: linear-gradient(-90deg, var(--alt-color-primary-opacity) 0%, var(--alt-color-primary-full-opacity) 100%);
}

.swiper-pagination {
  display: none;
}
@media (min-width: 1440px) {
  .swiper-pagination {
    display: block;
  }
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 66px;
}

.swiper-pagination-bullet {
  width: 62px;
  height: 4px;
  border-radius: 0;
  background-color: var(--basic-color);
  opacity: 1;
}
.swiper-pagination-bullet:hover {
  background-color: var(--alt-color-primary-opacity);
}

.swiper-pagination-bullet-active {
  background-color: var(--basic-color-primary);
}